/* global $ */

$('#js-scroll-top').on('click', e => {
	e.preventDefault();

	$('html, body').animate({
		scrollTop: 0,
	}, 800);

});

$('.js-footer-nav a').on('click', e => {
	e.preventDefault();

	const currentBlock = $(e.currentTarget).attr('href');
	const currentBlockOffset = $(currentBlock).offset().top;
	const offsetTop = $(currentBlock).children(':first').css('margin-top');

	$('html, body').animate({
		scrollTop: currentBlockOffset + parseFloat(offsetTop, 10),
	}, 500);

});



