/* global $ */

const $mainSlider = $('.js-acquaintance-slider');
const $listImg = $('.js-img-slider');
const $listBigImgSlider = $('.js-big-img-slider');

$mainSlider.on('init', function (){
	$listBigImgSlider.eq(5).show();
	$listImg.eq(5).addClass('is-active');
});

// init carousel
$mainSlider.slick({
	dots: true,
});

$listImg.on('click', e => {
	const $elem = $(e.currentTarget);
	const id = $elem.data('acquaintance-id');
	$listImg.removeClass('is-active');
	$elem.addClass('is-active');
	$listBigImgSlider.fadeOut();
	$(`.js-big-img-slider[data-acquaintance-id="${id}"]`).delay(310).fadeIn();
});




