/* global ymaps, $ */
/* eslint id-length: ["error", { "properties": "never" }] */

import 'selectize';

const init = () => {
	const myMap = new ymaps.Map(
		'js-map', // eslint-disable-line no-unused-vars
		{
			center: [56.139122, 40.427182],
			zoom: 10,
			controls: ['zoomControl'],
		},
		{
			suppressObsoleteBrowserNotifier: true, //  -       
			yandexMapDisablePoiInteractivity: true, //      
			// suppressMapOpenBlock: true //       .
		},
	);

	// const myPlacemark = new ymaps.Placemark(
	// 	[56.138888, 40.425369],
	// 	{
	// 		//        .
	// 		hintContent: '',
	// 		//      .
	// 		balloonContent: '',
	// 		iconCaption: ' , 43'
	// 	},
	// 	{
	// 		preset: 'islands#blueCircleDotIconWithCaption',
	// 		iconCaptionMaxWidth: '150'
	// 	}
	// );
	// myMap.geoObjects.add(myPlacemark);

	myMap.behaviors.disable('scrollZoom');
	ymaps.domEvent.manager
		.group(document.querySelector('#js-map'))
		.add(['click', 'mouseleave'], event => {
			if (event.get('type') === 'click') {
				myMap.behaviors.enable('scrollZoom');
			}else if (event.get('type') === 'mouseleave') {
				myMap.behaviors.disable('scrollZoom');
			}
		});
};

ymaps.ready(init);
// $('.js-map-form .region').selectize();
