/* global $ */

const $sliderSponsor = $('.js-slider-sponsor');
const $sliderSortA = $('.js-slider-sort a');

$sliderSponsor.slick({
	dots: true,
});

$sliderSortA.on('click', e => {
	$sliderSortA.removeClass('is-active');
	$(e.currentTarget).addClass('is-active');
});



