/* global $ */

const $goodsSlider = $('.js-goods-slider');
const $goodsImgs = $('.js-goods-img');
const $goodsBigImgList = $('.js-goods-big-img');
const $goodsDescBox = $('.js-goods-desc');
$goodsSlider.on('init', () => {
	$goodsImgs.eq(4).addClass('is-active');
	$goodsBigImgList.eq(4).find('img').show();
	$goodsDescBox.eq(4).show();
});

$goodsSlider.slick({
	dots: true,
});

$goodsImgs.on('click', e => {
	const $curImg = $(e.currentTarget);
	const curImgId = $curImg.data('goods-id');
	$goodsImgs.removeClass('is-active');
	$curImg.addClass('is-active');
	$goodsBigImgList.find('img').fadeOut();
	$goodsDescBox.fadeOut();
	$(`.js-goods-big-img[data-goods-id="${curImgId}"]`).find('img').delay(320).fadeIn();
	$(`.js-goods-desc[data-goods-id="${curImgId}"]`).delay(320).fadeIn();
});



